body {
  height: 100vh;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 20px;
  display: flex;
}

.loader-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #001eff;
  border-color: #001eff rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

